h1 {
    display: flex;
    flex-direction: row;
    height: 100px;
    align-items: center;
    justify-content: center;
    font-family: sans-serif;
    font-size: 22px;
    text-transform: uppercase;
    color: rgb(17, 113, 192);
}

.contact_us_form {
    display: flex;
    flex-direction: column;
    margin: 5% 10%;
    text-transform: uppercase;
    color: rgb(17, 113, 192);
    font-family: sans-serif;
    font-size: 14px;
}

.contact_us_form div {
    margin: 20px 0;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.contact_us_form label {
    color: rgb(17, 113, 192);
    font-family: sans-serif;
    font-size: 22px;
    text-transform: capitalize;
}

.contact_us_form input {
    width: 80%;
    height: 40px;
    font-size: 20px;
}

.button {
    display: flex;
    justify-content: flex-end;
    margin-top: 20px;
    padding-right: 10px;
}
